.RollDice {
    display: flex;
    flex-flow: column nowrap;
    min-height: 100vh;
}
.RollDice-container {
    display: flex;
    justify-content: center;
    align-content: center;
}
.RollDice-button {
    width: 15em;
    padding: 1.5em;
    border: 0px;
    border-radius: 10px;
    color: white;
    background-color: #273946;
    margin-top: 3em;
    align-self: center;
}
.RollDice button:hover {
    background-color: #2c6c9b;
    cursor: pointer;
}
